.display{
    
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 300px;
    height: 50px;
    margin-bottom: 20px;
    margin-top: 15px;
    font-size: 50px;
    border-radius: 5px;
    color: aliceblue;
    overflow-x: hidden;
    overflow-y: hidden;
}


.center{
    justify-content: center;
    display: flex;
}